@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@400;700&display=swap);
body {
  font-family: "Source Sans Pro", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 {
  color: rgb(236, 25, 56);
  font-family: "Oswald", sans-serif;
  font-weight: 700;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar {
  width: 100%;
  margin: 0;
  height: auto;
  margin: 0 auto;
  margin-bottom: 40px;
  position: -webkit-sticky; /* Safari */
  position: sticky;
  top: 0;
  padding: 10px 10px 0 10px;
  border-bottom: 3px solid rgb(235, 235, 235);
  background-color: white;
}

@media (min-width: 768px) {
  .navbar {
    padding: 0;
  }
}

.navbar__flex {
  width: 100%;
  text-align: center;
}

@media (min-width: 768px) {
  .navbar__flex {
    width: 80%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.navbar__brand {
  color: rgb(236, 25, 56);
  text-decoration: none;
  height: 30px;
  font-size: 25px;
}

@media (min-width: 768px) {
  .navbar__brand {
    display: flex;
    align-items: center;
  }
}

.navbar__brand:hover {
  color: rgb(236, 25, 56);
  text-decoration: none;
}

.navbar__brand-logo {
  height: 50px;
  padding-right: 10px;
}

.navbar__menu {
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media (min-width: 768px) {
  .navbar__menu {
    width: 50%;
  }
}
.navbar__menu-link {
  color: rgb(60, 60, 67);
  text-decoration: none;
  transition: all 0.3s ease-in-out;
}

.navbar__menu-link:hover {
  color: rgb(60, 60, 67);
  text-decoration: none;
  -webkit-transform: scale(1.2);
}

.navbar__menu-active {
  height: 80px;
  font-weight: 700;
  padding: 35px 10px 0;
  color: white;
  background-color: rgb(236, 25, 56);
}

.hero {
  text-align: center;
  padding-bottom: 10px;
}

@media (min-width: 762px) {
  .hero {
    display: flex;
    justify-content: space-around;
  }
}

.hero img {
  align-self: center;
  max-height: 400px;
  padding-bottom: 30px;
}

